import SvgIconsSize from "../Components/Profile2/IconProfile";
import BasicTextFields from "../Components/Profile3/Name";
import DisableElevation from "../Components/Profile2/Typo2";
import MiniDrawer from "../Components/Profile3/Avatar";

export default function ProfilePicture() {
    return (
        <React.Fragment>
            <ArrowBackIosRoundedIcon/>
                <Types/>
                    <SvgIconsSize/>
                <BasicTextFields/>
            <DisableElevation/>
        <MiniDrawer/>
    </React.Fragment>
    
    );
}