import React from "react";
import ArrowBackIosRoundedIcon from '@material-ui/icons/ArrowBackIosRounded';
import Types from "../Components/Profile2/Typo";
import SvgIconsSize from "../Components/Profile2/IconProfile";
import EditRoundedIcon from '@material-ui/icons/EditRounded';
import BasicTextFields from "../Components/Profile2/TextField";
import ContinueButtons from "../Components/Profile2/Typo2";

export default function MakeProfile() {
    return (
        <React.Fragment>
            <ArrowBackIosRoundedIcon />
                <Types />
                    <SvgIconsSize />
                        <EditRoundedIcon />
                <BasicTextFields />
            <ContinueButtons />
        </React.Fragment>
    );
}