import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Chip from '@material-ui/core/Chip';
import FaceIcon from '@material-ui/icons/Face';
import DoneIcon from '@material-ui/icons/Done';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    '& > *': {
      margin: theme.spacing(0.5),
    },
  },
}));

export default function SmallChips() {
  const classes = useStyles();

  const handleDelete = () => {
    console.info('You deselected the delete icon.');
  };

  const handleClick = () => {
    console.info('You selected the Chip.');
  };

  return (
    <div className={classes.root}>
      <Chip
        size="small"
        label="Thriller"
        onClick={handleClick}
        color="primary"
        onDelete={handleDelete}
      />
      <Chip
        size="small"
        label="Drama"
        onClick={handleClick}
        onDelete={handleDelete}
      />
      <Chip
        size="small"
        label="Comedy"
        onClick={handleClick}
        color="primary"
        onDelete={handleDelete}
      />
      <Chip
        size="small"
        label="Reality"
        onClick={handleClick}
        onDelete={handleDelete}
      />
      <Chip
       size="small"
       label="Fiction"
       onClick={handleClick}
       onDelete={handleDelete}
      />
      <Chip
        size="small"
        label="Romantic"
        onClick={handleClick}
        color="primary"
        onDelete={handleDelete}
      />
      <Chip
        size="small"
        label="Mythology"
        onClick={handleClick}
        onDelete={handleDelete}
      />
      <Chip
        size="small"
        label="Paranomal"
        onClick={handleClick}
        onDelete={handleDelete}
      />
    </div>
  );
}
