import React from "react";
import TypographyEdit from "../Components/Profile1/EditType";
import ArrowBackIosRoundedIcon from '@material-ui/icons/ArrowBackIosRounded';
import EditRoundedIcon from '@material-ui/icons/EditRounded';
import ChooseTypes from "../Components/Preferences/ChooseType";
import SmallChips from "../Components/Preferences/Chip1";
import FloatingActionButtons from "../Components/Preferences/Button";

export default function Preferences() {
    return (
        <React.Fragment>
            <TypographyEdit />
                <ArrowBackIosRoundedIcon /> <EditRoundedIcon />
                    <ChooseTypes />
            <SmallChips />
        <FloatingActionButtons />
    </React.Fragment>

    );
}