import React from "react";
import Types from "../Components/Profile1/Typography";
import OutlinedCard from "../Components/Profile1/Successfulcard";
import Icons from "../Components/Profile1/Plus";
import TypographyTheme from "../Components/Profile1/Addtype";
import TypographyEdit from "../Components/Profile1/EditType";
import ArrowBackIosRoundedIcon from '@material-ui/icons/ArrowBackIosRounded';
import EditRoundedIcon from '@material-ui/icons/EditRounded';

export default function Profile1() {
    return (
        <React.Fragment>
            <TypographyEdit />
                    <ArrowBackIosRoundedIcon /> <EditRoundedIcon />
                            <Types />
                        <Icons />
                    <TypographyTheme />
            <OutlinedCard />
        </React.Fragment>
    );
}